* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

img {
  display: block;
}

.country-list {
padding: 0;
margin: 0;
list-style: none;
}

.country-name {
  margin: 5px 0;
}

.country-list__item {
  display: flex;
  align-items: center;
  margin-bottom: 5px;
}

.country-list__text {
  margin: 0 0 0 10px;
}
